// src/pages/CakeList.js

import React, {
  useState,
  useEffect,
  useRef,
  useCallback,
  useMemo,
} from 'react';
import axios from 'axios';
import { useLocation } from 'react-router-dom';
import ProductCard from '../components/ProductCard';
import ProductModal from '../components/ProductModal';
import ScrollToTopButton from '../components/ScrollToTopButton';
import NavigationBar from '../components/NavigationBar';

function CakeList() {
  //      
  const [products, setProducts] = useState([]);
  const [selectedProduct, setSelectedProduct] = useState(null);
  const [searchTerm, setSearchTerm] = useState('');
  const [currentCategory, setCurrentCategory] = useState(null);

  //   
  const categories = useMemo(
    () => [
      '',
      '',
      '',
      '',
      '',
      '',
      ' ',
      '',
      '',
      '',
      '',
      '',
      ' ',
      '',
    ],
    []
  );

  const location = useLocation();
  const categoryRefs = useRef({});

  //      
  useEffect(() => {
    const handleScroll = () => {
      //       
      const scrollPosition = window.scrollY + 200;
      let currentCat = null;

      categories.forEach((category) => {
        const ref = categoryRefs.current[category];
        if (ref && ref.offsetTop <= scrollPosition) {
          currentCat = category;
        }
      });

      setCurrentCategory(currentCat);
    };

    window.addEventListener('scroll', handleScroll);
    return () => window.removeEventListener('scroll', handleScroll);
  }, [categories]);

  //     
  useEffect(() => {
    axios
      .get('https://fa55-147-45-74-27.ngrok-free.app/api/products/')
      .then((response) => {
        setProducts(response.data);
      })
      .catch((error) => {
        console.error('   :', error);
      });
  }, []);

  //      
  useEffect(() => {
    if (location.state && location.state.searchTerm) {
      setSearchTerm(location.state.searchTerm);
    } else {
      setSearchTerm('');
    }
  }, [location.state]);

  //      
  const scrollToCategory = useCallback((category) => {
    if (categoryRefs.current[category]) {
      categoryRefs.current[category].scrollIntoView({ behavior: 'smooth' });
      setCurrentCategory(category);
    }
  }, []);

  //        
  const openModal = useCallback((product) => {
    setSelectedProduct(product);
  }, []);

  const closeModal = useCallback(() => {
    setSelectedProduct(null);
  }, []);

  //    
  const filteredProducts = useMemo(() => {
    return searchTerm
      ? products.filter((product) =>
          product.name.toLowerCase().includes(searchTerm.toLowerCase())
        )
      : products;
  }, [products, searchTerm]);

  return (
    <div className="">
      {/*   */}
      <NavigationBar
        categories={categories}
        currentCategory={currentCategory}
        scrollToCategory={scrollToCategory}
      />

      {/*  */}
      <div className="container mx-auto px-4 pt-8">
        {categories.map((category) => {
          //    
          const categoryProducts = filteredProducts.filter(
            (product) => product.category === category
          );

          if (categoryProducts.length === 0) return null;

          return (
            <div
              key={category}
              ref={(el) => (categoryRefs.current[category] = el)}
              className="mb-8"
            >
              {/*   */}
              <h2 className="text-3xl font-bold text-black mb-4">{category}</h2>
              {/*   */}
              <div className="grid grid-cols-1 md:grid-cols-3 gap-8">
                {categoryProducts.map((product) => (
                  <ProductCard
                    key={product.id}
                    product={product}
                    openModal={openModal}
                  />
                ))}
              </div>
            </div>
          );
        })}
      </div>

      {/*    */}
      {selectedProduct && (
        <ProductModal product={selectedProduct} closeModal={closeModal} />
      )}

      {/*    */}
      <ScrollToTopButton />
    </div>
  );
}

export default CakeList;
