import React, { useState } from 'react';
import { useDispatch } from 'react-redux';

function CakeConstructor() {
  const [size, setSize] = useState('');
  const [flavor, setFlavor] = useState('');
  const [decorations, setDecorations] = useState([]);
  const dispatch = useDispatch();

  const handleDecorationChange = e => {
    const { value, checked } = e.target;
    setDecorations(prev =>
      checked ? [...prev, value] : prev.filter(dec => dec !== value)
    );
  };

  const handleSubmit = e => {
    e.preventDefault();
    const customCake = {
      id: Date.now(),
      name: ' ',
      description: `: ${size}, : ${flavor}, : ${decorations.join(', ')}`,
      price: 2000, //    
    };
    dispatch({ type: 'ADD_TO_CART', payload: customCake });
    //  
    setSize('');
    setFlavor('');
    setDecorations([]);
    //      
    alert('   !');
  };

  return (
    <div className="container mx-auto py-8 px-4">
      <h2 className="text-3xl font-bold text-primary mb-6 text-center font-serif">
          
      </h2>
      <form onSubmit={handleSubmit} className="max-w-xl mx-auto bg-white shadow-lg rounded-lg p-6">
        <div className="mb-4">
          <label className="block text-gray-700 font-bold mb-2 font-sans">:</label>
          <select
            value={size}
            onChange={e => setSize(e.target.value)}
            required
            className="w-full p-2 border border-gray-300 rounded"
          >
            <option value=""> </option>
            <option value=""></option>
            <option value=""></option>
            <option value=""></option>
          </select>
        </div>
        <div className="mb-4">
          <label className="block text-gray-700 font-bold mb-2 font-sans">:</label>
          <select
            value={flavor}
            onChange={e => setFlavor(e.target.value)}
            required
            className="w-full p-2 border border-gray-300 rounded"
          >
            <option value=""> </option>
            <option value=""></option>
            <option value=""></option>
            <option value=""></option>
          </select>
        </div>
        <div className="mb-6">
          <label className="block text-gray-700 font-bold mb-2 font-sans">:</label>
          <div className="flex flex-wrap">
            <label className="mr-4 mb-2">
              <input
                type="checkbox"
                value=""
                checked={decorations.includes('')}
                onChange={handleDecorationChange}
                className="mr-2"
              />
              
            </label>
            <label className="mr-4 mb-2">
              <input
                type="checkbox"
                value=""
                checked={decorations.includes('')}
                onChange={handleDecorationChange}
                className="mr-2"
              />
              
            </label>
            <label className="mr-4 mb-2">
              <input
                type="checkbox"
                value=""
                checked={decorations.includes('')}
                onChange={handleDecorationChange}
                className="mr-2"
              />
              
            </label>
            {/*      */}
          </div>
        </div>
        <button
          type="submit"
          className="w-full bg-primary text-secondary py-2 px-4 rounded hover:bg-red-700 font-bold text-lg transition duration-300 ease-in-out"
        >
            
        </button>
      </form>
    </div>
  );
}

export default CakeConstructor;
